import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { suggest } from '../funnelback';
import Clear from './finder__clear';

const maximumSuggestions = 5,
    //[keyCodeEnter, keyCodeEscape, keyCodeUp, keyCodeDown] = [13, 27, 38, 40],
    [keyCodeEnter, keyCodeEscape] = [13, 27];

function finder__query(props) {
    const [partialQuery, setPartialQuery] = useState(props.query.query);
    const [suggestions, setSuggestions] = useState([]);
    const [call, setCall] = useState({ cancel: () => {} });
    const [inputId] = useState(
        'finder--' +
            props.query.collection +
            '--' +
            Math.random()
                .toString(16)
                .slice(-4)
    );

    let textInput = null;
    function focusInput() {
        textInput.focus();
    }

    const clearQuery = () => {
        call.cancel();
        setPartialQuery('');
        setSuggestions([]);
        focusInput();

        const newQuery = props.query;
        newQuery.query = '';
        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const submitForm = () => {
        if (partialQuery) {
            call.cancel();
            setSuggestions([]);

            const newQuery = props.query;
            newQuery.query = partialQuery;
            newQuery.startRank = 1;
            props.update.query(newQuery);
            props.update.results(!props.update.updateState);
        }
    };

    const clear = partialQuery && (
        <Clear
            clear={() => {
                clearQuery();
            }}
        />
    );

    const suggestionsList = suggestions && suggestions.length > 0 && (
        <ul className="finder__query__suggestions">
            {suggestions.slice(0, maximumSuggestions).map(suggestion => (
                <li key={suggestion}>
                    <button
                        type="button"
                        onClick={() => {
                            setPartialQuery(suggestion);
                            setSuggestions([]);
                            focusInput();
                        }}
                    >
                        {suggestion}
                    </button>
                </li>
            ))}
        </ul>
    );

    const input = (
        <div className="finder__query__input">
            <label className="sr-only" htmlFor={inputId}>
                Search
            </label>
            <input
                autoComplete="off"
                id={inputId}
                name="query"
                placeholder={props.placeholder}
                ref={input => {
                    textInput = input;
                }}
                type="text"
                value={partialQuery}
                onKeyDown={e => {
                    switch (e.keyCode) {
                        case keyCodeEscape:
                            clearQuery();
                            break;
                        case keyCodeEnter:
                            submitForm();
                            break;
                    }
                }}
                onChange={e => {
                    // keep  what they're typing
                    setPartialQuery(e.target.value);

                    /**
                     * if we have a request to the suggestions service in progress,
                     * cancel it. Stops old suggestions overwriting new ones because
                     * the requests can't keep up with fast typing.
                     */
                    call.cancel();

                    if (e.target.value) {
                        // input is populated, ask for suggestions
                        const [suggestionsPromise, newCall] = suggest(
                            props.query.collection,
                            e.target.value
                        );

                        // update our request cancel function for the new request
                        setCall({
                            cancel: () => {
                                newCall.cancel();
                            },
                        });

                        suggestionsPromise
                            .then(data => setSuggestions(data))
                            .catch(() => setSuggestions([]));
                    } else {
                        // input is empty, empty suggestions
                        setSuggestions([]);
                    }
                }}
            />
            {suggestionsList}
            {clear}
        </div>
    );

    return (
        <fieldset className="finder__query">
            <div>
                {input}
                <button
                    type="submit"
                    className="finder__query__submit"
                    onClick={() => submitForm()}
                >
                    <span>
                        <span className="fal fa-search" aria-hidden="true" />{' '}
                        Find
                    </span>
                </button>
            </div>
        </fieldset>
    );
}

finder__query.propTypes = {
    placeholder: PropTypes.string,
    query: PropTypes.object,
    update: PropTypes.object,
};

export default finder__query;
